import React, {useState} from 'react';
import html2canvas from 'html2canvas';
import './App.css';

function App() {

  const [edit, updateEdit] = useState(true);

  const [title, setTitle] = useState(null);
  const [images, setImages] = useState([]);
  const [itemName, setItemName] = useState("");
  const [itemColor, setItemColor] = useState("");
  const [itemSize, setItemSize] = useState("");
  const [itemFabric, setItemFabric] = useState("면, 혼방");

  const toggleEdit = () => {
    updateEdit(!edit);
  }


  const onNameChange = (e) => {
    setItemName(e.target.value);
  }
  const onColorChange = (e) => {
    setItemColor(e.target.value);
  }
  const onSizeChange = (e) => {
    setItemSize(e.target.value);
  }
  const onFabricChange = (e) => {
    setItemFabric(e.target.value);
  }

  const onDeleteAll = () => {
    setImages([]);
  }

  const onDonwload = async () => {
    window.scrollTo(0,0);
    const canvas = await html2canvas(document.getElementsByClassName('frame')[0]
      , {
        allowTaint: true
        , useCORS: true
        , letterRendering: 1
        , scrollX: 0
        , scrollY: 0
        , scale: 1.0
      })

      const img = canvas.toDataURL('image/jpg');

      const element = document.createElement("a");
      element.href = img;
      element.download = 'detail.jpg';
      document.body.appendChild(element);
      element.click();
  }

  const addImage = (img) => {
    setImages(imgs => [...imgs, img])
    console.log(images);
  }

  const onDragEnter = (event) => {
    event.preventDefault();
  }

  const onDragLeave = (event) => {
    event.preventDefault();
  }

  const onDragOver = (event) => {
    event.preventDefault();
  }
  
  const onDrop = (event) => {
    event.preventDefault();
  }

  const onDropTitle = (event) => {
    if(edit) {
      const supportedFilesTypes = ['image/jpeg', 'image/png'];
      const files = event.dataTransfer.files;
      const file = files[0];
      if(supportedFilesTypes.indexOf(file.type) > -1) {
        const reader = new FileReader();
        reader.onload = e => setTitle(e.target.result);
        reader.readAsDataURL(file);
      }
    }
    else {
      alert('수정 모드에서만 이미지를 등록할 수 있습니다.');
    }
    event.preventDefault();
  }

  const onDropImages = (event) => {
    if(edit) {
      const supportedFilesTypes = ['image/jpeg', 'image/png'];
      const files = event.dataTransfer.files;
      for (let i=0;i<files.length;i++) {
        const file = files[i];
        if(supportedFilesTypes.indexOf(file.type) > -1) {
          const reader = new FileReader();
          reader.onload = e => addImage(e.target.result);
          reader.readAsDataURL(file);
        }
      }
    }
    else {
      alert('수정 모드에서만 이미지를 등록할 수 있습니다.');
    }
    event.preventDefault();
  }
  if(edit) {
    return (
      <div className="App"
      onDragEnter={onDragEnter}
      onDragLeave={onDragLeave}
      onDragOver={onDragOver}
      onDrop={onDrop}>
        <div className="topBar">
          <button className="edit" onClick={toggleEdit}>완료</button>
          <button className="remove" onClick={onDeleteAll}>이미지 삭제</button>
          <button className="download" disabled style={{backgroundColor: '#717c7d'}}>다운로드</button>
        </div>
        <div className="frame">
          <img style={{marginTop:30, marginBottom:70}} width="100%" src="http://ai.esmplus.com/jin465/assets/header.png"/>
            
            <div style={{marginTop:60, marginBottom:100}}>
            <input className="ItemName" placeholder="상품명" style={{width:"560px"}} onChange={onNameChange} value={itemName}/>
              <div className='Title'
                    onDragEnter={onDragEnter}
                    onDragLeave={onDragLeave}
                    onDragOver={onDragOver}
                    onDrop={onDropTitle}>
                <div style={{ 
                  width:'100%', height:'100%'
                , backgroundRepeat: 'no-repeat'
                , backgroundPosition: 'center center'
                , backgroundSize: 'cover'
                , backgroundImage: `url(${title})`}}>
                </div>
              </div>
                <div className="Option">
                  <div className="opt">
                    <div className="option_nm">
                      COLOR
                    </div>
                    <input className="option_val" placeholder="색상" style={{width:"400px"}} onChange={onColorChange} value={itemColor}/>
                  </div>
  
                  <div className="opt">
                    <div className="option_nm">
                      SIZE
                    </div>
                    <input className="option_val" placeholder="사이즈" style={{width:"400px"}} onChange={onSizeChange} value={itemSize}/>
                  </div>
  
                  <div className="opt">
                    <div className="option_nm">
                      FABRIC
                    </div>
                    <input className="option_val" placeholder="소재" style={{width:"400px"}} onChange={onFabricChange} value={itemFabric}/>
                  </div>
  
                </div>
              
              </div>
              <div className="Images"
                    onDragEnter={onDragEnter}
                    onDragLeave={onDragLeave}
                    onDragOver={onDragOver}
                    onDrop={onDropImages}
                    style={{
                      backgroundColor: '#ffd700de'
                    }}>
                  {images.map(img => <img width="780px" className="item" src={img}/>)}
              </div>
              <img style={{marginTop:70, marginBottom:30}} width="100%" src="http://ai.esmplus.com/jin465/assets/footer.png"/>
          </div>
        </div>
    );
  }
  else {
    return (
      <div className="App"
      onDragEnter={onDragEnter}
      onDragLeave={onDragLeave}
      onDragOver={onDragOver}
      onDrop={onDrop}>
        <div className="topBar">
          <button className="edit" onClick={toggleEdit}>수정</button>
          <button className="remove" disabled style={{backgroundColor: '#717c7d'}}>이미지 삭제</button>
          <button className="download" onClick={onDonwload}>다운로드</button>
        </div>
        <div className="frame">
          <img style={{marginTop:30, marginBottom:70}} width="100%" src="http://ai.esmplus.com/jin465/assets/header.png"/>
            
            <div style={{marginTop:60, marginBottom:100}}>
              <div className="ItemName">
                {itemName}
              </div>
              <div className='Title'
                    onDragEnter={onDragEnter}
                    onDragLeave={onDragLeave}
                    onDragOver={onDragOver}
                    onDrop={onDropTitle}>
                <div style={{ 
                  width:'100%', height:'100%'
                , backgroundRepeat: 'no-repeat'
                , backgroundPosition: 'center center'
                , backgroundSize: 'cover'
                , backgroundImage: `url(${title})`}}>
                </div>
              </div>
                <div className="Option">
                  <div className="opt">
                    <div className="option_nm">
                      COLOR
                    </div>
                    <div className="option_val">
                      {itemColor}
                    </div>
                  </div>
  
                  <div className="opt">
                    <div className="option_nm">
                      SIZE
                    </div>
                    <div className="option_val">
                      {itemSize}
                    </div>
                  </div>
  
                  <div className="opt">
                    <div className="option_nm">
                      FABRIC
                    </div>
                    <div className="option_val">
                      {itemFabric}
                    </div>
                  </div>
  
                </div>
              
              </div>
              <div className="Images"
                    onDragEnter={onDragEnter}
                    onDragLeave={onDragLeave}
                    onDragOver={onDragOver}
                    onDrop={onDropImages}>
                  {images.map(img => <img width="780px" className="item" src={img}/>)}
              </div>
              <img style={{marginTop:70, marginBottom:30}} width="100%" src="http://ai.esmplus.com/jin465/assets/footer.png"/>
          </div>
        </div>
    );
  }
  
}

export default App;
